import React, { Component, useState, useEffect, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { component } from 'picoapp';
import Countdown from 'react-countdown';

const formattedCountDown = ({ days, hours, minutes, seconds }) => {
  return (
    <div className="countdown">
      {days > 0 && `${days}D`}
      {hours}H {minutes}M {seconds}S
    </div>
  );
};

const CountdownComp = (props) => {
  const [limit, setLimit] = useState(null);

  useEffect(() => {
    if (props.parentNode.dataset.expires) {
      setLimit(Number(props.parentNode.dataset.expires) * 1000);
    }
  }, []);

  if (!limit) {
    return null;
  }

  return <Countdown date={Date.now() + limit} renderer={formattedCountDown} />;
};

export default component((node, ctx) => {
  const element = <CountdownComp ctx={ctx} parentNode={node} />;
  ReactDOM.render(element, node);
});
